<template>
  <div class="artist">
    <div class="hd">
      <img :src="artist_info.picUrl" alt="" />
      <div class="artist_info">
        <p class="name">{{artist_info.name}}</p>
        <p class="rank"></p>
        <div class="simple">
          <p class="nickname"></p>
          <p class="follow"></p>
          <p class="area"></p>
        </div>
      </div>
    </div>
    <h2>歌手简介</h2>
    <div class="intro"></div>
    <span> </span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      artist_info: null,
      hot_songs: [],
    };
  },
  created() {
    this.$axios("https://apis.netstart.cn/music/artists", {
      params: {
        id: this.$route.query.id,
      },
    }).then((res) => {
      this.artist_info = res.data.artist;
      this.hot_songs = res.data.hotSongs.slice(0, 5);
    });
  },
};
</script>

<style lang="less" scoped>
.artist {
  background-color: #fcfcfd;
  .hd {
    position: relative;
    padding-top: 82.9%;

    img {
      position: absolute;
      top: 0;
    }
    .artist_info {
    }
  }
  h2 {
  }
  .intro {
  }
  span {
  }
}
</style>