<template>
  <div class="play-view">
    <div
      class="mask"
      :style="{
        backgroundImage: `url(${
          song.picUrl ? song.picUrl : song.al.picUrl
        }?imageView=1&type=webp&thumbnail=253x0)`,
      }"
    ></div>
    <div class="zzz">
      <div class="head">
        <div class="info">
          <div class="return" @click.stop="$emit('return-home')">
            <!-- <i class="fa fa-chevron-down" aria-hidden="true"></i> -->
            <img src="@/assets/return_page.png" alt="" width="25rem" />
          </div>
          <div class="songinfo">
            <span>{{
              song.song?.artists[0].name
                ? song.song?.artists[0].name
                : song.ar[0].name
            }}</span>
            <span>{{ song.name }}</span>
          </div>
          <div class="share">
            <!-- <i class="fa fa-share-alt" aria-hidden="true"></i> -->
            <img src="@/assets/share_page.png" alt="" width="25rem" />
          </div>
        </div>
      </div>
      <div class="disc" v-if="!showlyric" @click="showlyric = true">
        <div class="needle" :class="{ move: !move }"></div>
        <div class="turn">
          <div class="rollwrap" :class="{ spin: playing }">
            <div class="songimg">
              <img
                :src="
                  song
                    ? `${
                        song.picUrl ? song.picUrl : song.al.picUrl
                      }?imageView=1&type=webp&thumbnail=253x0`
                    : ''
                "
                alt=""
              />
            </div>
          </div>
        </div>
        <!-- <span class="playbtn"> </span> -->
      </div>
      <!-- <LyricsItem
        v-else
        @click.native="showlyric = false"
        :lyricInfo="lyricInfo"
        :currentIndex="currentIndex"
        :song="song"
      /> -->
      <div v-else class="lyrics" ref="div" @click="showlyric = false">
        <ul :style="`transform: translateY(-${sum}rem)`" ref="ul">
          <li
            ref="li"
            v-for="(lyric, index) in lyricInfo"
            :key="index"
            :lyric="lyric"
            :style="{ color: currentIndex === index ? 'white' : '' }"
          >
            {{ lyric.text }}
          </li>
        </ul>
      </div>
      <div class="fixed-nav">
        <DragItem
          :duration="duration"
          :currentTime="currentTime"
          @update-time="$emit('update-time', $event)"
        />
        <div class="btn">
          <div class="model" @click.stop="$emit('change-model')">
            <!-- <img src="" alt=""> -->
            <!-- {{ model }} -->
            <svg
              v-if="model === '单曲'"
              t="1665146030140"
