              ></path>
              <path
                d="M792.576 303.5136l-86.8352-86.8352a20.48 20.48 0 1 1 28.8768-28.8768l86.8352 86.8352a20.48 20.48 0 0 1-28.8768 28.8768zM749.1584 715.5712A245.76 245.76 0 0 1 502.784 471.04v-14.7456c-15.7696-38.5024-17.6128-56.32-29.2864-67.7888a286.72 286.72 0 0 0-197.632-155.0336v40.96a245.76 245.76 0 0 1 178.7904 181.248c2.4576 3.6864 4.5056 7.5776 6.7584 11.264s0 2.048 0 3.072a286.72 286.72 0 0 0 286.72 284.672h61.44v-40.96zM235.52 228.352h-40.96v40.96h40.96z"
