                p-id="2252"
                fill="#ffffff"
              ></path>
            </svg>
          </div>
          <div @click="$emit('switch-song', 'prev')">
            <!-- <i class="fa fa-backward" aria-hidden="true"></i> -->
            <img src="@/assets/prev.png" alt="" width="25rem" />
          </div>
          <div>
            <!-- <i
              class="fa fa-pause"
              aria-hidden="true"
              v-if="playing"
              @click.stop="
                $emit('pause-song');
                move = false;
              "
            ></i> -->
            <img
              src="@/assets/pause.png"
              alt=""
              width="25rem"
              v-if="playing"
              @click.stop="
                $emit('pause-song');
                move = false;
              "
            />
            <!-- <i
              class="fa fa-play"
              aria-hidden="true"
              v-else
              @click.stop="
                $emit('play-song');
                move = true;
              "
            ></i> -->
            <img
              src="@/assets/play.png"
              alt=""
              width="25rem"
              v-else
              @click.stop="
                $emit('play-song');
                move = true;
              "
            />
          </div>
          <div @click="$emit('switch-song', 'next')">
            <!-- <i class="fa fa-forward" aria-hidden="true"></i> -->
            <img src="@/assets/next.png" alt="" width="25rem" />
          </div>
          <div @click="$emit('show-list')">
            <!-- <i class="fa fa-list-ul" aria-hidden="true"></i> -->
            <img src="@/assets/list_page.png" alt="" width="24rem" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DragItem from "@/components/DragItem.vue";
export default {
  components: {
    DragItem,
  },
  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    model: String,
  },
  data() {
    return {
      tmpTime: this.currentTime,
      drag: false,
      showlyric: false,
      move: true,
      tmpLyric: [],
      lyricInfo: [],
      copy: [],
      currentIndex: null,
      liHeight: [],
      total: 0,
      sum: 0,
      tmparr: [],
    };
  },

  created() {
    this.getLyric();
  },

  methods: {
    getLyric() {
      (this.lyricInfo = []),
        this.axios
          .get("https://apis.netstart.cn/music/lyric", {
            params: { id: this.song.id },
          })
          .then((res) => {
            this.tmpLyric = res.data.lrc.lyric
              .split("\n")
              .filter((o) => o)
              .map((o) =>
                o.match(/\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/)
              );
            this.tmpLyric.map((o) => {
              this.lyricInfo = [
                ...this.lyricInfo,
                {
                  time: o?.groups.mm * 60 + o?.groups.ss * 1,
                  text: o?.groups.text.trim(),
                },
              ];
            });
            this.lyricInfo = this.lyricInfo.filter((o) => o.text !== "");
            // this.lyricInfo = this.lyricInfo.filter((o) => o.text !== "");
            this.copy = [...this.lyricInfo].reverse();
          });
    },

    findCurrentLyric(currentTime) {
      this.a =
        this.copy.findIndex((o) => o.time < currentTime) === -1
          ? 0
          : this.copy.findIndex((o) => o.time < currentTime);
      this.currentIndex = this.copy.length - 1 - this.a;
    },
    getHeight(n) {
      this.tmparr = [...this.liHeight.slice(0, n)];
      this.tmparr.forEach((o) => {
        this.sum += o;
      });
    },
  },
  watch: {
    currentIndex(n) {
      this.sum = 0;
      this.getHeight(n);
      if (this.showlyric === true) {
        // console.log("li", this.$refs.li);
        if (this.$refs.li) {
          this.$refs.li.forEach((o) => {
            this.liHeight = [...this.liHeight, o.clientHeight];
          });
        }
      }
    },
    currentTime(n) {
      this.findCurrentLyric(n);
    },
    song() {
      this.getLyric();
      this.sum = 0;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.play-view {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: black;
  z-index: 400;

  .mask {
    height: 607px;
    position: absolute;
    width: 100%;

    top: 0;
    left: 0;
    z-index: -10;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    filter: blur(30px);
    transform: scale(1.5);
  }
  .zzz {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    .head {
      width: 100%;
      height: 100rem;
      display: flex;
      align-items: center;
      color: white;
      .info {
        width: 100%;
        height: 50rem;
        display: flex;
        justify-content: space-around;
        align-items: center;
        .return {
          font-size: 20rem;
        }
        .songinfo {
          width: 70%;
          display: flex;
          justify-content: center;
          flex-direction: column;
          text-align: center;
          font-size: 12rem;
          margin-top: -25rem;
          span {
            margin-bottom: 5rem;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
        .share {
          font-size: 20rem;
        }
      }
    }
    .disc {
      width: 247rem;
      height: 247rem;
      margin: 0 auto;
      position: relative;
      .needle {
        content: " ";
        position: absolute;
        z-index: 5;
        width: 73px;
        height: 118px;
        top: -50px;
        left: 112px;
        transform: rotate(-7deg);
        transform-origin: 10rem 10rem;
        background: url(@/assets/needle-ab.png) no-repeat;
        background-size: contain;
        transition: all 0.3s;
        &.move {
          transform: rotate(-35deg);
        }
      }
      .turn {
        width: 100%;
        height: 100%;
        &::before {
          content: " ";
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          z-index: 2;
          background: url(@/assets/blackCircle.png) no-repeat;
          background-size: contain;
        }
        .rollwrap {
          width: 184px;
          height: 184px;
          margin: -92px 0 0 -92px;
          position: absolute;
          left: 50%;
          top: 50%;
          z-index: 1;
          animation: spin 6s linear infinite;
          animation-play-state: paused;
          &.spin {
            animation-play-state: running;
          }
          .songimg {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            overflow: hidden;
            background: url(@/assets/disc_default.png) no-repeat;
            background-size: contain;
            img {
              width: 100%;
              vertical-align: middle;
            }
          }
        }
      }
      span.playbtn {
        width: 56px;
        height: 56px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
        background: url(@/assets/play.png) 0 0 no-repeat;
        background-size: contain;
        &::after {
          content: "";
          display: block;
          position: absolute;
        }
      }
      audio {
        display: block !important;
      }
    }

    .lyrics {
      height: 420rem;
      width: 247rem;
      overflow: hidden;
      display: flex;
      justify-content: center;
      position: relative;
      ul {
        width: 100%;
        position: absolute;
        top: 35%;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        li {
          width: 100%;
          text-align: center;
          font-size: 12rem;
          box-sizing: content-box;
          padding: 5rem 0;
          color: #999;
        }
      }
    }

    .fixed-nav {
      height: 100rem;
      width: 100%;
      padding: 10rem 10rem 20rem;
      background-color: transparent;
      z-index: 9;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: column;
      .drag {
        width: 100%;
        display: flex;
        justify-content: center;
        input {
          margin: 0 15rem;
          width: 60vw;
        }
      }
      .btn {
        width: 100%;
        display: flex;
        justify-content: space-around;
        padding: 5rem 0;
        & > div {
          width: 50rem;
          height: 30rem;
          border-radius: 14rem;
          display: flex;
          justify-content: center;
          align-items: center;
          i {
            color: white;
            // width: 30rem;
            // height: 30rem;
            font-size: 16rem;
          }
        }
        .model {
          svg {
            width: 30rem;
            height: 30rem;
          }
        }
      }
    }
  }
}
</style>
