<template>
  <div class="progressBar">
    <span class="nowTime"
      >{{
        Math.floor(currentTime / 60)
          .toString()
          .padStart(2, "0")
      }}{{
        ":" +
        Math.floor(currentTime % 60)
          .toString()
          .padStart(2, "0")
      }}</span
    >
    <div class="Bar">
      <input
        min="0"
        ref="input"
        type="range"
        :max="duration"
        :value="tmpCurrentTime"
        @input="
          draging = true;
          changeBar($event.target.value);
        "
        @change="
          draging = false;
          $emit('update-time', $event.target.value);
        "
      />
      <div class="virtualBar" ref="circle">
        <div class="btn"></div>
      </div>
    </div>
    <span class="totalTime"
      >{{
        Math.floor(duration / 60)
          .toString()
          .padStart(2, "0")
      }}{{
        ":" +
        Math.floor(duration % 60)
          .toString()
          .padStart(2, "0")
      }}</span
    >
  </div>
</template>

<script>
export default {
  props: {
    duration: Number,
    currentTime: Number,
  },
  data() {
    return {
      tmpCurrentTime: this.currentTime,
      draging: false,
    };
  },

  methods: {
    changeBar(n) {
      this.tmpCurrentTime = n;
      this.$refs.circle.style.width = (n / this.duration) * 100 + "%";
    },
  },

  mounted() {
    this.$refs.circle.style.width =
      (this.currentTime / this.duration) * 100 + "%";
  },

  watch: {
    currentTime(n) {
      if (!this.draging) {
        this.tmpCurrentTime = n;
        this.$refs.circle.style.width = (n / this.duration) * 100 + "%";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.progressBar {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;

  display: flex;
  justify-content: center;
  align-items: center;
  span {
    pointer-events: none;
    padding: 0 15rem;
  }
  .Bar {
    position: relative;
    width: 250rem;
    height: 1rem;
    background-color: rgb(107, 96, 96);
    input {
      position: absolute;
      display: block;
      left: 0;
      z-index: 999;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
    .virtualBar {
      height: 100%;
      position: absolute;
      left: 0;
      background-color: rgb(255, 255, 255);
      .btn {
        display: block;
        width: 5rem;
        height: 5rem;
        background-color: #fff;
        position: absolute;
        right: -2rem;
        top: -2rem;
        border-radius: 50%;
      }
    }
  }
}
</style>