<template>
  <li
    class="home-card"
    @click="$router.push({ path: '/playlist', query: { id: item.id } })"
    @updata-song="$emit('updata-song', $event)"
    @get-playlist="$emit('get-playlist', $event)"
  >
    <img :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=370x0`" alt="" />
    <h5>{{ item.name }}</h5>

    <span>{{ count }}</span>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  computed: {
    count: function () {
      return this.item.playCount > 100000000
        ? `${(this.item.playCount / 100000000).toFixed(1)}亿`
        : this.item.playCount > 10000
        ? `${(this.item.playCount / 10000).toFixed(1)}万`
        : this.item.playCount;
    },
  },
};
</script>

<style lang="less" scoped>
.home-card {
  width: 32.7%;
  margin-bottom: 16px;
  position: relative;
  h5 {
    padding: 6px 2px 0 6px;
    font-size: 13rem;
    color: #333333;
  }
  span {
    position: absolute;
    right: 5px;
    top: 2px;
    z-index: 3;
    padding-left: 13px;
    color: #fff;
    font-size: 12px;
    background-position: 0;
    background-repeat: no-repeat;
    background-size: 11px 10px;
    text-shadow: 1px 0 0 rgb(0 0 0 / 15%);
    background-image: url(@/assets/listening.svg);
  }
}
</style>