<template>
  <transition
    name="custom-classes-transition"
    enter-active-class="animate__animated animate__slideOutUp"
  >
    <div class="play-nav">
      <div class="fixed-nav">
        <div class="left">
          <div class="circle">
            <img
              :class="{ spin: playing }"
              :src="
                song
                  ? `${
                      song.picUrl ? song.picUrl : song.al.picUrl
                    }?imageView=1&type=webp&thumbnail=60x0`
                  : ''
              "
            />
          </div>
          <h3>
            {{ song.name }}
            -
            {{
              song.song?.artists[0].name
                ? song.song?.artists[0].name
                : song.ar[0].name
            }}
          </h3>
        </div>
        <div class="right">
          <div class="pause_play">
            <img
              src="@/assets/Player, pause.png"
              alt=""
              v-if="playing"
              @click.stop="$emit('pause-song')"
            />
            <img
              src="@/assets/Player, play.png"
              alt=""
              v-else
              @click.stop="$emit('play-song')"
            />
            <!-- <i
              v-if="playing"
              @click.stop="$emit('pause-song')"
              class="fa fa-pause"
              aria-hidden="true"
            ></i>
            <i
              v-else
              @click.stop="$emit('play-song')"
              class="fa fa-play"
              aria-hidden="true"
            ></i> -->
            <canvas ref="canvas" width="40" height="40"></canvas>
          </div>
          <div class="show_list" @click.stop="$emit('show-list')">
            <img src="@/assets/list.png" alt="">
            <!-- <i class="fa fa-list-ul" aria-hidden="true"></i> -->
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
    duration: [Number, String],
    currentTime: [Number, String],
  },
  data() {
    return {};
  },
  computed: {
    precent() {
      return this.currentTime / this.duration;
    },
  },
  methods: {
    zzz() {
      console.log(1);
    },
  },

  watch: {
    precent(n) {
      var canvas = this.$refs.canvas;
      var context = canvas.getContext("2d");
      context.strokeStyle = "rgb(0, 0, 0)";
      context.clearRect(0, 0, 40, 40);
      context.beginPath();
      context.arc(20, 20, 19, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.play-nav {
  height: 50rem;
  .fixed-nav {
    height: 50rem;
    width: 96%;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    background-color: white;
    z-index: 300;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: black;
    padding: 5rem 0 9rem;
    .left {
      width: 80%;
      display: flex;
      align-items: center;
      .circle {
        margin-right: 15px;
        border-radius: 50%;
        width: 50rem;
        height: 50rem;
        position: relative;
        img {
          border-radius: 50%;
          animation: spin 6s linear infinite;
          animation-play-state: paused;
          position: absolute;
          left: 0;
          top: -8rem;
          &.spin {
            animation-play-state: running;
          }
        }
      }
      h3 {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        width: 180rem;
        font-size: 14rem;
      }
    }
    .right {
      width: 30%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .pause_play {
        width: 40rem;
        height: 40rem;
        position: relative;
        img {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          z-index: 18;
          display: flex;
          justify-content: center;
          align-items: center;
          // font-size: 16rem;
          width: 26rem;
          height: 26rem;
        }
        canvas {
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          margin: auto;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      .show_list {
        margin-right: 6rem;
        img {
          width: 25rem;
          height: 25rem;
        }
      }
    }
  }
}
</style>