<template>
  <div class="search">
    <form action="">
      <div class="inputcover">
        <i class="searchicon"></i>
        <input
          type="text"
          autocomplete="off"
          placeholder="搜索歌曲、歌手、专辑"
          v-model="keyword"
          @focus="
            searchResult = null;
            show = true;
            bestMatches = [];
          "
          @keyup.enter="getSearchResult(keyword)"
        />
        <!-- <label class="holder">搜索歌曲、歌手、专辑</label> -->
        <div
          class="figure"
          @click="
            keyword = '';
            searchResult = null;
            bestMatches = [];
          "
        ></div>
      </div>
    </form>
    <div v-show="loading" class="loadingImg">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div class="default" v-show="!keyword">
      <div class="result">
        <h3>热门搜索</h3>
        <ul>
          <SearchItem
            v-for="result in results"
            :key="result.first"
            :result="result"
            @Search-Result="getSearchResult"
          />
        </ul>
      </div>
      <div class="getHistory">
        <ul class="list">
          <HistoryItem
            v-for="item in history"
            :key="item"
            :item="item"
            @Search-Result="getSearchResult"
            @Delete-History="deleteHistory"
          />
        </ul>
      </div>
    </div>
    <div class="content" v-if="getSuggest?.length && !searchResult && show">
      <h3>搜索"{{ keyword }}"</h3>
      <ul>
        <SuggestItem
          v-for="suggest in getSuggest"
          :key="suggest.keyword"
          :suggest="suggest"
          @Search-Result="getSearchResult"
        />
      </ul>
    </div>
    <div class="match" v-if="searchResult">
      <div class="matchlist">
        <h3>最佳匹配</h3>
        <ul>
          <MatchItem
            v-for="(match, index) in bestMatches"
            :key="index"
            :match="match"
          ></MatchItem>
        </ul>
      </div>
      <div class="songlist">
        <div class="songitem">
          <ResultItem
            v-for="(result, index) in searchResult?.songs"
            :result="result"
            :key="index"
            :songs="searchResult?.songs"
            @updata-song="$emit('updata-song', $event)"
            @get-playlist="$emit('get-playlist', $event)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchItem from "@/components/SearchItem.vue";
import SuggestItem from "@/components/SuggestItem.vue";
import ResultItem from "@/components/ResultItem.vue";
import HistoryItem from "@/components/HistoryItem.vue";
import MatchItem from "@/components/MatchItem.vue";
export default {
  components: {
    SearchItem,
    SuggestItem,
    ResultItem,
    HistoryItem,
    MatchItem,
  },
  data() {
    return {
      show: false,
      keyword: "",
      results: [],
      getSuggest: [],
      searchResult: null,
      history: JSON.parse(localStorage.getItem("history")) || [],
      bestMatches: [],
      loading: false,
    };
  },
  created() {
    // 获得默认的热门搜索
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.results = res.data.result.hots;
    });

    this.debouncedGetSearchSuggests = this._.debounce(
      this.getSearchSuggests,
      200
    );
  },
  methods: {
    // 获得搜索建议的数组
    getSearchSuggests(keyword) {
      this.loading = true;
      this.getSuggest = [];
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: {
            keywords: keyword,
            type: "mobile",
          },
        })
        .then((res) => {
          this.loading = false;
          this.getSuggest = this.keyword ? res.data.result.allMatch : [];
        });
    },
    // 获得搜索的数组以及最佳匹配
    getSearchResult(keywords) {
      // console.log(keywords);
      this.show = false;
      this.keyword = keywords;
      this.history = [...new Set([keywords, ...this.history])];
      localStorage.setItem("history", JSON.stringify(this.history));
      this.loading = true;
      // //获取最佳匹配
      Promise.all([
        this.axios.get("https://apis.netstart.cn/music/cloudsearch", {
          params: { keywords },
        }),
        this.axios.get("https://apis.netstart.cn/music/search/multimatch", {
          params: { keywords },
        }),
      ]).then(([p, n]) => {
        // console.log(p, n);
        this.loading = false;
        this.searchResult = p.data.result;
        // this.bestMatches = n.data.result;
        this.bestMatches =
          n.data.result.artist !== undefined
            ? [...this.bestMatches, { artist: n.data.result.artist }]
            : this.bestMatches;
        this.bestMatches =
          n.data.result.album !== undefined
            ? [...this.bestMatches, { album: n.data.result.album }]
            : this.bestMatches;
        this.bestMatches =
          n.data.result.playlist !== undefined
            ? [...this.bestMatches, { playlist: n.data.result.playlist }]
            : this.bestMatches;
        // console.log(n.data.result.artist !== undefined);
      });
    },

    // 删除历史记录
    deleteHistory(n) {
      this.history = [...this.history.filter((o) => o !== n)];
    },
  },
  watch: {
    keyword: function (n) {
      console.log(n);
      if (n) {
        this.bestMatches = [];
        this.searchResult = null;
        this.debouncedGetSearchSuggests(n);
      } else {
        this.getSuggest = [];
        this.searchResult = null;
      }
    },

    history: function (n) {
      localStorage.setItem("history", JSON.stringify(n));
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  height: 100%;
  position: relative;
  form {
    padding: 15rem 10rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    .inputcover {
      position: relative;
      width: 100%;
      height: 30px;
      padding: 0 30px;
      box-sizing: border-box;
      background: #ebecec;
      border-radius: 30px;
      i.searchicon {
        position: absolute;
        left: 0;
        top: 9px;
        margin: 0 8px;
        vertical-align: middle;
        width: 13px;
        height: 13px;
        background-image: url(@/assets/search.svg);
      }
      input {
        width: 100%;
        height: 30rem;
        line-height: 18rem;
        background: rgba(0, 0, 0, 0);
        font-size: 14rem;
        color: #333;
        border: none;
        outline: none;
      }
      label {
        position: absolute;
        left: 33rem;
        top: 5rem;
        font-size: 14rem;
        color: #c9c9c9;
        background: rgba(0, 0, 0, 0);
        pointer-events: none;
      }
      .figure {
        background-image: url(@/assets/cancel.svg);
        width: 14rem;
        height: 14rem;
        position: absolute;
        right: 6rem;
        top: 8rem;
        text-align: center;
      }
    }
  }
  .loadingImg {
    width: 30rem;
    height: 30rem;
    display: flex;
    justify-content: center;
    // align-items: center;
    position: fixed;
    top: 31%;
    left: calc(50% - 15rem);
    background-color: #fff;
    z-index: 999;
    img {
    }
  }
  .result {
    padding: 15rem 10rem 0;
    h3 {
      font-size: 12rem;
      line-height: 12rem;
      color: #666;
    }
    ul {
      margin: 10rem 0 7rem 0;
    }
  }
  .content {
    h3 {
      height: 50px;
      margin-left: 10px;
      padding-right: 10px;
      font-size: 15px;
      line-height: 50px;
      color: #507daf;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
  }
}
</style>