<template>
  <div class="home">
    <h3>编辑推荐</h3>
    <ul class="editor-recommend">
      <HomeCard
        v-for="item in personalized"
        :key="item.id"
        :item="item"
        @updata-song="$emit('updata-song', $event)"
        @get-playlist="$emit('get-playlist', $event)"
      />
    </ul>

    <h3>最新音乐</h3>
    <ul class="hot-song">
      <SongItem
        @updata-song="$emit('updata-song', $event)"
        @get-playlist="$emit('get-playlist', $event)"
        v-for="song in newsong"
        :key="song.id"
        :song="song"
        :playing="playing"
        :songId="songId"
        :newsong="newsong"
      />
    </ul>
    <!-- <footer>
      <div class="wrapper">
        <div class="logo">
          <img src="@/assets/qishui.png" alt="" />
        </div>
        <div class="openapp">打开APP，发现更多好音乐 ></div>
      </div>
    </footer> -->
  </div>
</template>

<script>
import HomeCard from "@/components/HomeCard.vue";
import SongItem from "@/components/SongItem.vue";

export default {
  components: {
    HomeCard,
    SongItem,
  },
  props: {
    playing: Boolean,
    songId: Number,
  },
  data() {
    return {
      personalized: [],
      newsong: [],
    };
  },

  created() {
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized", {
        params: { limit: 6 },
      }),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
    ]).then(([p, n]) => {
      this.personalized = p.data.result;
      this.newsong = n.data.result;
    });
  },
};
</script>

