<template>
  <li
    href=""
    class="listsong"
    @click="
      $emit('updata-song', song);
      $emit('get-playlist', listsongs);
    "
  >
    <div class="rank">{{ index + 1 }}</div>
    <div class="textdec">
      <div class="left">
        <p>
          {{ song.name }}
          <span v-if="song.alia.length !== 0">({{ song.alia[0] }})</span>
        </p>
        <p>
          <!-- <i v-if="song.sq !== null"></i> -->
          {{ song.ar[0].name }}-{{ song.al.name }}
        </p>
      </div>
      <div class="right">
        <span></span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    index: Number,
    song: Object,
    listsongs: Array,
  },
};
</script>

<style lang="less" scoped>
.listsong {
