  display: flex;
  text-decoration: none;
  .rank {
    width: 40rem;
    font-size: 17rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  //   &:nth-child(1),
  //   &:nth-child(2),
  //   &:nth-child(3) {
  //     .rank {
  //       color: #df3436;
  //     }
  //   }
  &:nth-child(n) {
    .rank {
      color: #888888;
    }
  }
  .textdec {
    width: 335rem;
    padding: 6px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    .left {
      width: 323rem;
      p {
