<template>
  <li
    class="song-info"
    @click="
      $emit('updata-song', song);
      $emit('get-playlist', newsong);
    "
  >
    <!-- @click="$router.push({ path: '/play', query: { id: song.song.id } })" -->
    <div class="textdec">
      <div class="left">
        <p>{{ song.name }}</p>
        <p>
          <i v-if="song.song.sqMusic !== null"></i>
          <!-- 判断是不是有SQ song.sqmusic === null -->
          {{ song.song.artists[0].name }}-{{ song.name }}
        </p>
      </div>
      <div class="right">
        <span class="play" v-if="song.id !== songId"></span>
        <div v-else class="play-img active" :class="{ playing: playing }">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
    songId: Number,
    newsong: Array,
  },
};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
  from {
    transform: rotateX(15deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
.song-info {
  width: 365rem;
  box-sizing: content-box;
  padding-left: 10rem;
  .textdec {
    width: 365rem;
    padding: 6px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    .left {
      width: 323rem;
      p {
        &:nth-child(1) {
          font-size: 17rem;
          color: #333333;
        }
        &:nth-child(2) {
          font-size: 13rem;
          color: #888888;
          i {
            display: inline-block;
            width: 12px;
            height: 8px;
            margin-right: 4px;
            background-image: url(@/assets/index_icon_2x.png);
            background-size: 166px 97px;
          }
        }
      }
    }
    .right {
      box-sizing: border-box;
      width: 42rem;
      // height: 42rem;
      padding: 0 10px;
      display: flex;
      align-items: center;

      span.play {
        display: inline-block;
        width: 22rem;
        height: 22rem;
        background-image: url(@/assets/index_icon_2x.png);
        background-size: 166px 97px;
        background-position: -24px 0;
      }
      .play-img {
        // margin-right: 10rem;
        width: 22px;
        height: 22px;
        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;
          span {
            width: 11%;
            height: 100%;
            background: red;
            animation: playingrunning 0.8s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;
            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }

          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }
}
</style>