<template>
  <div>
    <header>
      <div class="return" @click="$router.push({ path: '/' })">
        <i class="fa fa-chevron-left" aria-hidden="true"></i>
      </div>
      <div
        class="backimg"
        :style="{
          backgroundImage: `url(${playlist.coverImgUrl}?imageView=1&type=webp&thumbnail=253x0)`,
        }"
      ></div>
      <div class="wrapper">
        <div class="left">
          <img :src="playlist.coverImgUrl" alt="" />
          <span class="ls_icon">歌单</span>
          <i class="ls_num">2310.6万</i>
        </div>
        <div class="right">
          <h2>{{ playlist.name }}</h2>
          <div class="author">
            <a href="">
              <div class="avatar">
                <img :src="playlist.creator?.avatarUrl" alt="" />
                <span></span>
              </div>
              {{ playlist.creator?.nickname }}
            </a>
          </div>
        </div>
      </div>
    </header>
    <div class="playlist">
      <h3 class="title">歌曲列表</h3>
      <div class="list">
        <ListItem
          v-for="(song, index) in listsongs"
          :key="index"
          :index="index"
          :song="song"
          :listsongs="listsongs"
          @updata-song="$emit('updata-song', $event)"
          @get-playlist="$emit('get-playlist', $event)"
        />
        <div class="more-hold"></div>
        <div class="more-wrap">
          <div class="more-text">查看更多歌曲，请下载客户端</div>
        </div>
      </div>
    </div>
    <div class="goodComment">
      <div class="talk">
        <div class="comment">
          <h4 class="comtitle">精彩评论</h4>
          <div class="commlist">
            <CommentItem
              v-for="(comment, index) in comments"
              :key="index"
              :comment="comment"
            />
          </div>
        </div>
        <div class="bot-dec">
          <div class="more-comment">
            <span>查看全部{{ playlist.commentCount }}评论</span>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="collect-playlist">
      <div class="wrapper">
        <div>
          <a href="">收藏歌单</a>
        </div>
      </div>
    </div> -->
  </div>
</template>

<script>
import ListItem from "@/components/ListItem.vue";
import CommentItem from "@/components/CommentItem.vue";
export default {
  components: {
    ListItem,
    CommentItem,
  },
  data() {
    return {
      playlist: [],
      listsongs: [],
      comments: [],
    };
  },
  methods: {
    getPlayList(id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        })
        .then((res) => {
          // console.log(res);
          this.playlist = res.data.playlist;
          this.listsongs = res.data.playlist.tracks.slice(0, 10);
        });
    },
    getComments(id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: { id },
        })
        .then((res) => {
          // console.log(res);
          this.comments = res.data.hotComments;
        });
    },
  },
  created() {
    this.getPlayList(this.$route.query.id);
    this.getComments(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
header {
  height: 186rem;
  padding: 30px 10px 30px 15px;
  overflow: hidden;
  position: relative;
  .return {
    width: 40rem;
    height: 28rem;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16rem;
    i {
      color: #fff;
    }
  }
  .backimg {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    filter: blur(20rem);
    transform: scale(1.5);
  }
  .wrapper {
    display: flex;
    position: relative;
    z-index: 2;
    .left {
      width: 126rem;
      height: 126rem;
      position: relative;
      background-color: #e2e2e3;
      span.ls_icon {
        position: absolute;
        z-index: 3;
        top: 10px;
        left: 0;
        padding: 0 8px;
        height: 17px;
        color: #fff;
        font-size: 9px;
        text-align: center;
        line-height: 17px;
        background-color: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 17px;
        border-bottom-right-radius: 17px;
      }
      i.ls_num {
        position: absolute;
        right: 2px;
        top: 0;
        z-index: 3;
        padding-left: 15px;
        color: #fff;
        font-size: 12px;
        background-position: 0;
        background-repeat: no-repeat;
        background-size: 11px 10px;
        text-shadow: 1px 0 0 rgb(0 0 0 / 15%);
        background-image: url(@/assets/listening.svg);
      }
    }
    .right {
      flex: 1 1 auto;
      width: 1%;
      margin-left: 16rem;
      padding-top: 3rem;
      h2 {
        padding-top: 1rem;
        font-size: 17rem;
        line-height: 1.3;
        color: #fefefe;
        height: 44rem;
      }
      .author {
        display: block;
        position: relative;
        overflow: hidden;
        a {
          display: inline-block;
          color: hsla(0, 0%, 100%, 0.7);
          text-decoration: none;
          cursor: pointer;
          .avatar {
            position: relative;
            display: inline-block;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            vertical-align: middle;
            margin-right: 5px;
            img {
              border-radius: 50%;
              width: 100%;
              vertical-align: middle;
            }
            span {
              position: absolute;
              right: -5px;
              bottom: 0;
              width: 12px;
              height: 12px;
              background-image: url(@/assets/usericn_2x.png);
              background-repeat: no-repeat;
              background-size: 75px auto;
              background-position: -40px 0;
            }
          }
        }
      }
    }
  }
}
.playlist {
  h3.title {
    height: 23rem;
    line-height: 23rem;
    padding: 0 10rem;
    font-size: 12rem;
    color: #666;
    background-color: #eeeff0;
  }
  .list {
    position: relative;
    .more-hold {
      width: 100%;
      height: 29rem;
    }
    .more-wrap {
      position: absolute;
      z-index: 10;
      left: 0;
      right: 0;
      bottom: 0;
      .more-text {
        margin-top: 137px;
        color: #999;
        font-size: 14px;
        text-align: center;
      }
    }
  }
}

.goodComment {
  // padding-bottom: 61px;
  .talk {
    .comment {
      .comtitle {
        padding: 5rem 10rem;
        background-color: rgb(242, 242, 242);
        color: #666;
        font-size: 12rem;
        font-weight: 400;
      }
    }
    .bot-dec {
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      .more-comment {
        margin-left: 50px;
        span {
          display: block;
          margin-right: 50px;
          padding: 18px 0;
          font-size: 14px;
          color: #999;
          text-align: center;
          line-height: 1;
        }
      }
    }
  }
}
.collect-playlist {
  position: fixed;
  background-color: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 20;
  .wrapper {
    padding: 11px 30px;
    display: flex;
    div {
      flex: 1 1 12px;
      position: relative;
      a {
        display: block;
        width: 100%;
        height: 40px;
        line-height: 40px;
        border-radius: 40px;
        font-size: 18px;
        text-align: center;
        background: rgb(255, 58, 58);
        color: rgb(255, 255, 255);
        text-decoration: none;
      }
    }
  }
}
</style>